; }

body {
  font-family: 'Oswald', Arial, sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  color: var(--font);
  background: #fff; }

/* a:visited,
 a:hover*/
a {
  color: var(--font); }

.icon {
  display: inline-block;
  width: 30px;
  height: 30px;
  max-width: 30px;
  margin-right: 10px; }

svg {
  width: 1em;
  height: 1em; }

.main {
  padding-bottom: 50px; }

@media (min-width: 576px) {
  .aside {
    order: 1; }
  .content {
    order: 2; } }

@media (min-width: 992px) {
  .col-lg-20p {
    flex: 0 0 auto;
    width: 20%; } }

.rounded {
  border-radius: 4px; }

.color-green {
  color: var(--base); }

#map {
  width: 100%;
  height: 400px;
  padding: 0;
  margin: 0 0 30px 0; }

#map .ymaps-layers-pane,
#map .ymaps-2-1-79-ground-pane {
  filter: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg'><filter id='grayscale'><feColorMatrix type='matrix' values='0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0 0 0 1 0'/></filter></svg>#grayscale");
  -webkit-filter: grayscale(100%); }

.header {
  margin: 0 0 20px 0;
  position: relative; }

.header-service-bar {
  background: #fff;
  display: flex;
  flex-wrap: wrap; }

.header-service-bar__row {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  flex-wrap: wrap;
  padding: 12px 0; }

.header-service-panel {
  display: flex;
  gap: 15px;
  justify-content: space-around; }

.shop-cart-info {
  flex: 0 0 auto; }

.shop-cart-info__link {
