  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 5px; }

.sura-icon-heart-fill {
  font-size: 16px;
  color: var(--green-dark);
  vertical-align: middle;
  margin-right: 3px; }

.favorite__count {
  color: var(--font);
  line-height: 16px; }

